.drag-and-drop-container {
  text-align: center;
  margin: 20px;
}

.drag-and-drop-box {
  width: 100%;
  height: 5px;
  border-radius: 12px;

  border: 2px dashed gray; /* Initial border style */
  background-color: lightgray; /* Initial background color */
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 16px;
  color: #333;
}

.drag-and-drop-box:hover {
  border: 2px solid green; /* Border style when hovered */
}

.drag-and-drop-box.dragging {
  border: 2px solid green; /* Border style when dragging */
  background-color: lightgreen; /* Background color when dragging */
  color: #fff; /* Text color when dragging */
}
